<script>
  import api from 'src/packages/Api'
  import Loading from 'partials/Loading'
  
  export default {
    name: 'update-testimonial',
    data () {
      return {
        testimonial: {},
        id: 0,
        sort: '',
        active: '',

        loaded: false
      }
    },
    components: {
      Loading
    },
    
    async created () {
      try {
        const request = await api.get(`testimonials/${this.$route.params.id}`)

        this.testimonial = request.data
        this.id = request.data.id
        this.sort = request.data.sort
        this.active = request.data.active ? true : false

        this.loaded = true
      } catch(error) {
        this.$store.dispatch('addToastMessage',
          { text: this.$t(`errors.${error.data.status_code}`) }
        )
        this.$router.push({ name: 'testimonials' })
      }
    },
    methods: {
      update () {
        this.$validator.validateAll().then((result) => {
          if (result) {
            const data = {
              sort: this.sort,
              active: this.active
            }

            api.put(`testimonials/${this.id}`, data).then(response => {
              this.$store.dispatch('addToastMessage',
                { text: this.$t('testimonial.updated'), type: 'success' }
              )
              this.$router.push({ name: 'testimonials' })
            }).catch(error => {
              this.$store.dispatch('addToastMessage',
                { text: this.$t('errors.any'), error }
              )
            })
          }
        })
      }
    }
  }
</script>

<template>
  <loading v-if="!loaded"/>
  <div class="form-elements" v-else>
    <div class="row">
      <div class="col-md-12">
        <vuestic-widget :headerText="'menu.testimonials.update' | translate">

          <div class="row row-reverse">
            <div class="col-6">
              <ul class="rtl-list">
                <li style="">
                  <b class="expand-icon">{{ $t('testimonial.name') }}</b> : 
                  {{ testimonial.name }}
                </li>
                <li>
                  <b class="expand-icon">{{ $t('testimonial.phone') }}</b> : 
                  {{ testimonial.phone }}
                </li>
                <li>
                  <b class="expand-icon">{{ $t('testimonial.email') }}</b> : 
                  {{ testimonial.email }}
                </li>
              </ul>
            </div>
            <div class="col-6" style="direction: rtl;text-align: right;">
              {{ testimonial.content }}
            </div>
          </div>

          <div class="row row-reverse mt-5">
            <div class="col">
              <div class="form-group with-icon-right"
                   :class="{'has-error': errors.has('sort')}">
                <div class="input-group">
                  <input
                    id="sort"
                    name="sort"
                    v-model="sort"
                    v-validate="'required'"
                    type="number"
                    required/>
                  <i
                    class="fa fa-exclamation-triangle icon-right input-icon"
                    v-show="errors.has('sort')"></i>
                  <label class="control-label" for="sort">{{'faq.sort'
                    | translate}}</label><i class="bar"></i>
                  <small v-show="errors.has('sort')"
                         class="help text-danger">{{
                    errors.first('sort')
                    }}
                  </small>
                </div>
              </div>
            </div>

            <div class="col">
              <vuestic-switch v-model="active">
                <span slot="falseTitle">
                  {{'custom.inactive' | translate}}
                </span>
                <span slot="trueTitle">
                  {{'custom.active' | translate}}
                </span>
              </vuestic-switch>
            </div>

            <div class="col">
            </div>
          </div>

          <div class="row">
            <div class="col col text-center mt-4">
              <button class="btn btn-success"  @click="update">
                {{'buttons.update' | translate}}
              </button>
            </div>
          </div>
        </vuestic-widget>        
      </div>
    </div>
  </div>
</template>
